  padding-left: 10px;
}

.resultWrapper li {
  padding: 10px;
  border: 1px solid black;
  margin-top: 10px;
}

.withBuffer {
  display: block;
  margin-top: 20px;
}

.rightMargin {
  margin-right: 20px;
}



